
  import Vue from 'vue'
  import Tarefa from '../components/Tarefas/Tarefa.vue'

  export default Vue.extend({
    name: 'Home',

    components: {
        Tarefa
    },
      data(){
        return{
          campoInput: null,          
        }
      },
      methods:{
        handleAddTarefa()
        {
          this.$store.commit('AddTarefa',this.campoInput);
          this.campoInput = null;
        }
      }
  })
